<template>
  <div></div>
</template>

<script>
export default {
  name: 'subscriber',
  props: {
    stream: {
      type: OT.Stream,
      required: true
    },
    session: {
      type: OT.Session,
      required: true
    },
    opts: {
      type: Object,
      required: false
    }
  },
  data() {
    return {
    }
  },
  mounted: function() {
    const publisherproperties = {
      width: '100%',
      resolution: "640x480",
      frameRate:15,
      publishVideo: true,
      height: 450,
      name: this.$store.state.DataAdditional.fullname
    }

    const opentokSession = this.$store.state.stateOpentokSession
    const subscriber = opentokSession.subscribe(this.stream, this.$el, publisherproperties, (err) => {
      if (err) {
        this.$emit('error', err);
      } else {
        this.$emit('subscriberConnected', subscriber);
      }
    });
    this.$emit('subscriberCreated', subscriber);

    subscriber.on('videoDisableWarning', (event) => {
      // console.log(event);
      this.$store.commit("commitCallQuality", event)
    });
    subscriber.on('videoDisableWarningLifted', (event) => {
      // console.log(event);
      this.$store.commit("commitCallQuality", event)
    });
    subscriber.on('videoEnabled', (event) => {
      // console.log(event);
      this.$store.commit("commitCallQuality", event)
    });
  }
};
</script>



