<template>
   <div class="BXscripts__BxTabScript">
      <v-expansion-panel
         v-for="(data, i) in listScripts"
         :key="i"
         v-model="panel[i]"
         popout
      >
         <v-expansion-panel-content>
            <div slot="header"  @click="certPlanTab(i, data.unit)">{{data.unit}}</div>
            <v-card>
               <v-card-text class="bxContent">
                  <div class="titletask">
                     Coach Task
                  </div>
                  <div class="bxCoachTask">
                     <div class="coachTask" v-for="(data, i) in unitCert" :key="i">
                         <div class="bxchekBx">
                             <input 
                                 type="checkbox" 
                                 v-model="Selected" 
                                 :id="data.id" 
                                 :value="data.id" 
                                 @change="CheckBox($event, data.id)"
                                 :name="data.id" 
                                 class='box'
                              />
                              <label :for="data.id"></label>
                         </div>

                        <div  
                           class="task" 
                           :class="{ active: data.active}" 
                           :id = 'data.id'
                           
                           v-html="data.script"
                        ></div>
                     </div>
                     
                  </div>
               </v-card-text>
            </v-card>
         </v-expansion-panel-content>
      </v-expansion-panel>
   </div>
</template>

<script>
   export default {
      props:[
         'StudentId'
      ],
      data() {
         return {
            panel: [],
            unitCert: null,
            taskDone: '',
            Selected: [],
         };
      },
      computed: {
         listScripts () {
            const data = this.$store.state.DataScriptB2i.data
            const filteredData = [];
           
            data.filter((item) =>{
               var i = filteredData.findIndex(x => (x.unit == item.unit));
               if(i <= -1){
                     filteredData.push(item);
               }
               return null;
            });
           
            return filteredData
         },
         // Checktest(){
         //    let array = this.Selected ? this.Selected : ''
         //    array.forEach(function(item, index){
         //       document.getElementById(item).classList.add('active'); 
         //    });
         // },

         CheckGetMark(){
            return this.$store.state.GetMarkDataScriptB2i ? this.$store.state.GetMarkDataScriptB2i.data : null
         },

         FilterCheckGetMark(){
            const data = this.CheckGetMark 
            const items =[]

            for (var i in data ){
               this.Selected.push(data[i].script_id)
            }

         }
      },
      
      methods: {
         certPlanTab (i, unit) {
            
            this.activeIndex = i
            const unitCert = this.$store.state.DataScriptB2i.data.filter(x => x.unit === unit)
            
            this.unitCert = unitCert
            // this.Checktest
         },


         CheckBox(e, id){
            if (e.target.checked) {
               //console.log("Checked " + id)
               const MarkData = {
                  username : this.StudentId,
                  script_id : id
               }
               this.$store.dispatch('POST_MARK_DATA_B2C_SCRIPTS', MarkData);
            }else{
                console.log("Unchecked " + id)

               const data = this.CheckGetMark.filter(x => x.script_id === id)
               const ids = {
                  id : data[0].id,
                  username : this.StudentId
               }
               //console.log(data)
               this.$store.dispatch('DELETE_UNMARK_DATA_B2C_SCRIPTS', ids);
            }            
             
         }
      },
      created(){
         console.log(this.FilterCheckGetMark)
      }
   };
</script>




