<template lang="html">
  <div class="">
    <div class="BXscripts__BxChatbox">
       <div class="BXscripts__BxChatbox__BxAvatarChat">
          <div class="AvatarChat" @click="Modal_Picture">
             <v-avatar size = "80">
                <img
                   :src="this.DataUpcoming[0].avatar"
                   alt="avatar"
                >
             </v-avatar>
          </div>
          <div class="NameAvatar" v-if="authUser">
             <p>{{this.DataUpcoming[0].student_name}}</p>
             <span>Student</span>
          </div>
          <div class="NameAvatar" v-if="!authUser">
             <p>{{this.DataUpcoming[0].coach_name}}</p>
             <span>Coach</span>
          </div>
       </div>
       <div class="BXscripts__BxChatbox__BxChatList" ref="bottomChat">
         <div class="opentok__chat" :class="data.class" v-for="(data, index) in textArray">
           <div class="opentok__chat-each">
             <span>{{ data.from }}</span>
             <p>{{ data.text }}</p>
           </div>
         </div>
       </div>
       <div class="BXscripts__BxChatbox__BXChatInput">
          <div class="ChatInput">
             <input @keyup.enter="sendText" @keyup="clearNotifChat" type="text" placeholder="Type some messages..." v-model="textChat">
             <button @click="sendText"><div class="semi__btn">Send</div></button>
          </div>
       </div>
    </div>
    <v-dialog v-model="DialogUpAvatar" width="250" content-class="DialogUpAvatar">
        <v-card style="border-radius: 3px;">
            <v-avatar size="250">
                <img :src="this.AvatarUpModal">
            </v-avatar>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>

export default {
  props:[
     "DataUpcoming"
  ],
  data () {
    return {
      textArray: [],
      textChat: "",
      DialogUpAvatar: false,
      AvatarUpModal : null,
    }
  },
  mounted () {
    // TO HANDLE OPENTOK CHAT
    this.stateOpentokSession.on("signal", (event) => {
      if (this.stateOpentokSession && event.from.connectionId != this.stateOpentokSession.connection.id) {
        // Signal received from another client
        let textChat = event.data
        this.textArray.push({
          from: "Student",
          text: textChat,
          class: "you"
        })
        this.scrollToEnd()
        this.$store.commit('muttNotifChat', textChat)
      }
      if (this.stateOpentokSession && event.from.connectionId == this.stateOpentokSession.connection.id) {
        // Signal received from another client
        let textChat = event.data
        this.textArray.push({
          from: "Me",
          text: textChat,
          class: "me"
        })
        this.scrollToEnd()
      }
    });
  },
  computed: {
    authUser () {
      if (this.$store.state.UserRoleId === 'coach'){
         return true
      } else if (this.$store.state.UserRoleId === 'student') {
         return false
      }
    },
    getDataUpcoming () {
      return this.$store.state.DashUpcomingSessions.new_appointment[0]
    },
    stateOpentokSession () {
      return this.$store.state.stateOpentokSession
    }
  },
  methods: {
    sendText () {
      this.stateOpentokSession.signal(
        {
          to: this.stateOpentokSession,
          data: this.textChat
        },
        function(error) {
          if (error) {
            console.log("signal error ("
                         + error.name
                         + "): " + error.message);
          } else {
            console.log("signal sent.");
          }
        }
      );
      this.textChat = ""
    },
    clearNotifChat () {
      this.$store.commit('muttNotifChat', null)
    },
    scrollToEnd () {
      this.$nextTick(() => {
         this.$refs.bottomChat.scrollTop = 100000;
      });
    },
    Modal_Picture(avatar){
      this.DialogUpAvatar = true
      this.AvatarUpModal = this.DataUpcoming[0].avatar
    },
  },
}
</script>

<style lang="css">
</style>



