<template>
  <div>
    <v-snackbar
     v-model="NotifWaiting"
     color="blue darken-3"
     :timeout="timeout"
     top
     style="margin:auto;"
     class="SesNotif"
     >
      <p style="margin-bottom:0px;text-align:center;width: 100%;font-style:italic;font-size:.9em;">Waiting for Student to Join the Session, Please remain in session until the end to receive tokens.</p>
    </v-snackbar>
    <div class="Bx-CountAlert">
       <div class="BxCountDownt">
          <div class="countdowns">
             <Countdown
              v-on:event_child="handleExit2"
              :startTime = datetimestart
              :endTime = datetimeend
              trans = '{
                  "day":"Day",
                  "hours":"Hours",
                  "minutes":"Minuets",
                  "seconds":"Seconds",
                  "expired":"Event has been expired.",
                  "running":"Till the end of event.",
                  "upcoming":"Till start of event.",
                  "status": {
                      "expired":"Session Expired",
                      "running":"Session is Running",
                      "upcoming":"Upcoming Session"
                  }
              }'
             />
          </div>
       </div>
    </div>
    <div class="student__coach__vid">
      <div id="publisher" ref="myid"></div>
      <div id="subscribers" v-for="stream in streams" :key="stream.streamId">
        <subscriber @error="errorHandler" :stream="stream" :session="session"></subscriber>
      </div>
    </div>
    <div id="screen-preview"></div>
    <div class="box__warn--pulse" style="width:100%;" v-if="callWarning">Poor connection, video will be turned off</div>
    <div class="opentok__nav">
      <div class="opentok__nav-icon" @click="screenSharing">
        <i class="ag-icon ag-icon-remove-pip"></i>
      </div>
      <div class="opentok__nav-icon" @click="toggleCamera">
        <i class="ag-icon ag-icon-camera" v-if="!videoVal"></i>
        <i class="ag-icon ag-icon-camera-off" v-if="videoVal"></i>
      </div>
    </div>
    <!-- <input type="text" name="" v-model="textChat">
    <button @click="sendText">Send</button> -->
  </div>
</template>

<script>
import OT from '@opentok/client';
import Publisher from './Publisher.vue';
import Subscriber from './Subscriber.vue';
import Countdown from './Countdown.vue';
import moment from 'moment';

const errorHandler = (err) => {
  alert(err.message);
};

export default {
  name: 'session',
  components: {
    Publisher,
    Subscriber,
    Countdown
  },
  props: [
     "GetSessionStudent",
     "DataUpcoming"
  ],
  data: () => ({
    AuthCoach: false,
    streams: [],
    session: null,
    publisher: null,
    textArray: [],
    textChat: "",
    videoVal: true,
    datetimestart:'',
    datetimeend: '',
    NotifWaiting: true,
    timeout: 6000000,
    visitor: null,
    callWarning: false
  }),
  mounted () {
    // OPENTOK STUFF
    const opentokSessionId = this.$store.state.stateStartSessionCch.user[0].session
    const opentokApiKey = this.$store.state.opentokApiKey
    const opentokToken = this.$store.state.stateStartSessionCch.user[0].token

    // this.session = OT.initSession(opentokApiKey, opentokSessionId);
    const opentokSession = OT.initSession(opentokApiKey, opentokSessionId);
    this.$store.commit("commitOpentokSession", opentokSession)

    this.stateOpentokSession.connect(opentokToken, (err) => {
      if (err) {
        errorHandler(err);
      }
    });

    this.stateOpentokSession.on('streamCreated', (event) => {
      this.streams.push(event.stream);
      // console.log("New stream in the session: " + event.stream.streamId);
      // console.log(event);
      this.visitor = event.stream.streamId
    });

    this.stateOpentokSession.on('streamDestroyed', (event) => {
      const idx = this.streams.indexOf(event.stream);
      // console.log("Stream stopped. Reason: " + event.reason);
      // console.log(event);
      this.visitor = event.reason
      if (idx > -1) {
        this.streams.splice(idx, 1);
      }
    });

    const publisherproperties = {
      width: '100%',
      resolution: "320x240",
      frameRate:15,
      publishVideo: true,
      height: 150,
      name: "Me"
    }
    this.publisher = OT.initPublisher(this.$refs.myid, publisherproperties, (err) => {
      if (err) {
        this.$emit('error', err);
      } else {
        this.$emit('publisherCompleted');
      }
    });

    const publish = () => {
      this.stateOpentokSession.publish(this.publisher, (err) => {
        if (err) {
          console.log(err);
        } else {
          console.log(this.publisher);
        }
      });
    };

    if (this.stateOpentokSession && this.stateOpentokSession.isConnected()) {
      publish();
    }
    if (this.stateOpentokSession) {
      this.stateOpentokSession.on('sessionConnected', publish);
    }

    // TO HANDLE OPENTOK CHAT
    this.stateOpentokSession.on("signal", (event) => {
      console.log(event);

      let textChat = event.data
      this.textArray.push({
        "coach": textChat
      })
      localStorage.setItem('opentokChat', this.textArray);
      console.log(this.textArray);
    });
    // OPENTOK STUFF END

    //TO DECIDE WHICH ROUTE AFTER SESSION DONE
    if(this.$route.path === '/coach/live/opentok'){
       this.AuthCoach = true
    }else{
        this.AuthCoach = false
    }
  },
  created () {
    this.datetimestart = this.FormatStartTime + " " + this.GET_DataUpcoming.start_time
    this.datetimeend = this.FormatStartTime + " " + this.GET_DataUpcoming.end_time

    this.publisherproperties.name = this.$store.state.DataAdditional.fullname
  },
  computed: {
    stateOpentokSession () {
      return this.$store.state.stateOpentokSession
    },
    StoreDataUpcoming(){
       return this.$store.state.DashUpcomingSessions
    },
    DataUpcoming () {
       const items = []
       const dataValue = this.StoreDataUpcoming.new_appointment

       dataValue.forEach(function (value, key) {
          moment(value.date).format('MMMM, Do YYYY')
          items.push(dataValue);
          // console.log(key +" " + value.date+ " " + value.start_time);
       });

       function compare(a, b){
          if (a.date < b.date) return -1;
          if (a.date > b.date) return 1;
          if (a.start_time < b.start_time) return -1;
          if (a.start_time > b.start_time) return 1;
       }
       return [...dataValue].sort(compare)
    },
    GET_DataUpcoming(){
       return this.DataUpcoming[0]
    },
    FormatStartTime () {
       return this.GET_DataUpcoming.date ? moment(this.GET_DataUpcoming.date).format('MMMM D, YYYY') : ''
    },
    GET_ProfileName () {
      // return this.publisherproperties.name = this.$store.state.DataAdditional.fullname
    },
    handleNotifCallQuality () {
      return this.$store.state.stateCallQuality
    }
  },
  methods: {
    errorHandler,
    handleExit2() {
       this.stateOpentokSession.unpublish(this.publisher);
       this.streams = [];
       this.stateOpentokSession = null;
       this.publisher = null;
       // redirect to index
       if (this.AuthCoach){
         this.$router.push("/coach/live/summary");
       }else{
         this.$router.push("/student/live/summary");
       }
    },
    screenSharing() {
      OT.checkScreenSharingCapability((response) => {
        if(!response.supported || response.extensionRegistered === false) {
          // This browser does not support screen sharing.
        } else if (response.extensionInstalled === false) {
          // Prompt to install the extension.
        } else {
          // Screen sharing is available. Publish the screen.
          const publisher = OT.initPublisher('screen-preview',
            {videoSource: 'screen'},
            (error) => {
              if (error) {
                // Look at error.message to see what went wrong.
              } else {
                this.stateOpentokSession.publish(publisher, (error) => {
                  if (error) {
                    // Look error.message to see what went wrong.
                  }
                });
              }
            }
          );
        }
      });
    },
    toggleCamera () {
      this.videoVal = !this.videoVal
      this.publisher.publishVideo(this.videoVal)
    },
    sendText () {
      this.stateOpentokSession.signal(
        {
          to: this.stateOpentokSession,
          data: this.textChat
        },
        function(error) {
          if (error) {
            console.log("signal error ("
                         + error.name
                         + "): " + error.message);
          } else {
            console.log("signal sent.");
          }
        }
      );
      this.textChat = ""
    },
  },
  watch: {
    visitor (val) {
      if (val == null || val == "clientDisconnected") {
        this.NotifWaiting = true
      } else {
        this.NotifWaiting = false
      }
    },
    handleNotifCallQuality (val) {
      if (val) {
        if (val.type == "videoDisableWarning") {
          this.callWarning = true
        } else {
          this.callWarning = false
        }
      }
    }
  },
  beforeDestroy () {
    this.stateOpentokSession.unpublish(this.publisher);
  }
};
</script>

<style lang="scss">
  @import '../../styles/icons.css';

  .student__coach__vid  {
    width: 100%;
    min-height: 450px;
    position: relative;

    & #publisher {
      width: 150px !important;
      position: absolute;
      top: 38px;
      right: 5px;
      z-index: 2;
    }
  }
  #screen-preview {
    width: 100% !important;
    // height: 350px !important;
  }
  .opentok__nav {
    width: 100%;
    height: 20px;
    margin: 40px 0 0;
    display: flex;
    justify-content: space-around;

    &-icon {
      display: flex;

      i {
        color: $darkgrey;
        font-size: 4em;
        cursor: pointer;
        transition: .2s;

        &:hover {
          color: $darkblue;
          // font-weight: 600;
        }
      }
    }
  }
</style>



