<template>
  <div></div>
</template>

<script>
import OT from '@opentok/client';

export default {
  name: 'publisher',
  props: {
    session: {
      type: OT.Session,
      required: false
    },
    opts: {
      type: Object,
      required: false
    }
  },
  data() {
    return {
      publisherproperties: {
        width: '100%',
        resolution: "640x480",
        frameRate:15,
        publishVideo: true,
        height: 450,
        // name: "<?php echo $this->auth_manager->get_name();?>"
      }
    }
  },
  mounted: function() {
    const publisher = OT.initPublisher(this.$el, this.publisherproperties, (err) => {
      if (err) {
        this.$emit('error', err);
      } else {
        this.$emit('publisherCompleted');
      }
    });
    this.$emit('publisherCreated', publisher);
    const publish = () => {
      this.session.publish(publisher, (err) => {
        if (err) {
          this.$emit('error', err);
        } else {
          this.$emit('publisherConnected', publisher);
        }
      });
    };
    if (this.session && this.session.isConnected()) {
      publish();
    }
    if (this.session) {
      this.session.on('sessionConnected', publish);
    }
  }
};
</script>



