<template>
   <div class="BXliveSession">
      <div class="BXliveSession__BxGroup opentok__live__container">
        <h2 class="hidden-sm-and-down" style="color:rgb(112, 185, 247);" v-if="handleStdType">Trial Student</h2>
         <StudentLiveSession v-if="roleCondition"/>
         <CoachLiveSession v-if="!roleCondition"/>
         <StudyData :DataUpcoming="DataUpcoming" v-if="stateResizeScreen == 3"/>
         <StudyDataMobile v-if="stateResizeScreen == 1 || stateResizeScreen == 2"/>
      </div>
      <OpentokTabMenu :DataUpcoming='DataUpcoming'/>
   </div>
</template>

<script>
   import moment from'moment'
   import StudentLiveSession from '../../student-components/opentok-live-session/LiveSession.vue'
   import CoachLiveSession from '../../coach-components/opentok-live-session/LiveSession.vue'
   import OpentokTabMenu from '../../components/opentok-live/OpentokTab.vue'
   import StudyData from '../../components/agora-live/StudyData/StudyData.vue'
   import StudyDataMobile from '../../components/agora-live/StudyData/StudyDataMobileOpentok.vue'

   export default {
      components: {
         StudentLiveSession,
         CoachLiveSession,
         OpentokTabMenu,
         StudyData,
         StudyDataMobile
      },
      data() {
         return {

         }
      },
      mounted () {
        this.$nextTick( () => {
           window.addEventListener('resize', this.getWindowWidth);
           window.addEventListener('resize', this.getWindowHeight);

           this.getWindowWidth()
           this.getWindowHeight()
        });
      },
      computed: {
        stateResizeScreen () {
          const screen = this.$store.state.stateResizeScreen
          if (screen == null) {
            return 0
          } else {
            return screen
          }
        },
        roleCondition () {
          const role = this.$store.state.UserRoleId

          if (role == "student") {
            return true
          } else {
            return false
          }
          return true
        },
        handleStdType () {
           const data = this.$store.state.DataAdditional.type

           if (data == "regular") {
             return false
           } else {
             return true
           }
        },
        StoreDataUpcoming(){
           return this.$store.state.DashUpcomingSessions
        },
        DataUpcoming () {
           const items = []
           const dataValue = this.StoreDataUpcoming.new_appointment

           dataValue.forEach(function (value, key) {
              moment(value.date).format('MMMM, Do YYYY')
              items.push(dataValue);
              // console.log(key +" " + value.date+ " " + value.start_time);
           });

           function compare(a, b){
              if (a.date < b.date) return -1;
              if (a.date > b.date) return 1;
              if (a.start_time < b.start_time) return -1;
              if (a.start_time > b.start_time) return 1;
           }
           return [...dataValue].sort(compare)
        },
      },
      methods: {
        getWindowWidth(event){
           this.windowWidth = document.documentElement.clientWidth;

           if (this.windowWidth <= 599){
           //this for resolution phone only
           const resizeScreen = 1
           this.$store.commit('commitResizeScreen', resizeScreen)
           }else if(this.windowWidth <= 768){
              //this for resolution Ipad Potrait
              const resizeScreen = 2
              this.$store.commit('commitResizeScreen', resizeScreen)
           }else if(this.windowWidth >= 1024){
              //this for resolution Dekstop-up
              const resizeScreen = 3
              this.$store.commit('commitResizeScreen', resizeScreen)
           }
        },
        getWindowHeight(event){
           this.windowHeigt = document.documentElement.clientHeight;
        }
      },
      beforeDestroy() {
         window.removeEventListener('resize', this.getWindowWidth);
         window.removeEventListener('resize', this.getWindowHeight);
      }
   }
</script>

<style lang="scss">
  @import '../../styles/AgoraLiveSession.scss';
  @import './OpentokLiveSession.scss';
</style>



