<template>
   <div class="BXscripts__BxcoachNote">
      <div class="boxNotif">
         <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
			>
				<p>{{NotifText}}</p>
				<!-- <v-btn dark flat @click="snackbar = false">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>
      <div class="BXscripts__BxcoachNote--TextInput">
         <v-textarea
            v-model="CoachNote"
            rows = 10
            outline
            placeholder="Type Some Note..."
         ></v-textarea>
      </div>
      
      <div class="BXscripts__BxcoachNote--BtnInput">
         <v-btn class="btnSqureBlue" :disabled="CoachNote == '' " @click="PostCoachNote">UPDATE NOTE</v-btn>
      </div>

      <div class="BXscripts__BxcoachNote--BxNoteCard">
         <content-loader
            :animate="true"
            :height="300"
            :width="350"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
            v-if="!myData"
            
         >
            <rect x="10" y="40" rx="0" ry="0" width="330" height="100" />
            <rect x="10" y="170" rx="0" ry="0" width="330" height="100" />
            
         </content-loader>
        
         <div class="bxCard" v-for="(n, index) in this.DataSession" :key="index" v-else>
            <div class="bxCard__bxHeader">
               <div class="bxCard__bxHeader__BxAvatar">
                  <v-avatar>
                     <img
                        :src="n.coach_profile.avatar"
                        alt="avatar"
                     >
                  </v-avatar>
                  <div class="BxName">
                     <p>{{n.coach_profile.fullname}}</p>
                     <p>Last Sessions Notes</p>
                  </div>
                  
               </div>
               <div class="bxCard__bxHeader__BxDateLastSession">
                  <p>{{n.date | dateFormat}}</p>
                  <p>{{n.start_time}}</p>
               </div>
            </div>

            <div class="bxCard__BxNOTE">
               {{n.cch_notes}}
            </div>
         </div>
        
      </div>
   </div>
</template>

<script>
   import moment from 'moment'
   import { ContentLoader } from "vue-content-loader"
   export default {
      components:{
         ContentLoader
      },
      props:[
         'DataUpcoming'
      ],
      data() {
         return {
            myData :null,
            CoachNote:'',
            snackbar: false,
            NotifColor:'',
            NotifText:'',
            timeout: 2000,
         }
      },
      created() {
         this.CoachNote = this.GetCoachNote.CoachNote ? this.GetCoachNote.CoachNote : ''
      },
      mounted() {
         const Username = this.DataUpcoming[0].username
         this.$store.dispatch("GET_COACH_SEE_STUDENT_NOTE", Username);
         setTimeout(() => {
            this.myData = 'Example Data';
         }, 2000)
      },
      computed: {
         GetCoachNote(){
            return this.$store.state.CoachNote ? this.$store.state.CoachNote : ''
         },
         GET_SessionHistory(){
            return this.$store.state.CoachSeeStudentNote? this.$store.state.CoachSeeStudentNote.data : null
         },
         DataSession() {
            const items = []
            const dataValue = this.GET_SessionHistory

            dataValue.forEach(function (value, key) {
               moment(value.date).format('MMMM, Do YYYY')
               items.push(dataValue);
               // console.log(key +" " + value.date+ " " + value.start_time);
            });

            function compare(a, b){
               if (a.date > b.date) return -1;
               if (a.date < b.date) return 1;
               if (a.start_time < b.start_time) return -1;
               if (a.start_time > b.start_time) return 1;
            }

            return [...dataValue].sort(compare)
         }
      },
      methods: {
         PostCoachNote(){
            this.$store.commit('MuttCoachNote',{
               CoachNote : this.CoachNote
            })

            this.snackbar = true
            this.NotifColor= 'success'
            this.NotifText = 'Update Coach Note is Successfully'
         }
      },
      filters: {
         dateFormat: function (value) {
            if (value) {
               return moment(String(value)).format('MMM D YYYY')
            }
         }, 
      }
   }
</script>



